* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

:root {
  --bg-dark: #1a1a2e;
  --bg-darker: #16213e;
  --text-primary: #fff;
  --text-secondary: #a0a0a0;
  --accent: #0f3460;
  --success: #4ade80;
  --danger: #ef4444;
  --border-color: #333;
}

html, body {
  width: 100%;
  height: 100%;
  overflow: hidden;
}

body {
  background: var(--bg-dark);
  color: var(--text-primary);
  flex-direction: column;
  font-family: -apple-system, BlinkMacSystemFont, Segoe UI, Roboto, Oxygen, Ubuntu, sans-serif;
  display: flex;
}

.scanner-header {
  background: var(--bg-darker);
  border-bottom: 1px solid var(--border-color);
  z-index: 100;
  justify-content: space-between;
  align-items: center;
  height: 56px;
  padding: 12px 16px;
  display: flex;
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
}

.scanner-header h1 {
  text-align: center;
  flex: 1;
  margin: 0;
  font-size: 1.1rem;
  font-weight: 600;
}

.back-btn, .settings-btn {
  color: var(--text-primary);
  cursor: pointer;
  background: none;
  border: none;
  border-radius: 8px;
  justify-content: center;
  align-items: center;
  width: 44px;
  height: 44px;
  transition: background .2s;
  display: flex;
}

.back-btn:hover, .settings-btn:hover {
  background: #ffffff1a;
}

.camera-container {
  background: #000;
  flex: 1;
  margin-top: 56px;
  position: relative;
  overflow: hidden;
}

.camera-container resize-observer, .camera-container capture-photo {
  width: 100%;
  height: 100%;
  display: block;
}

.camera-container capture-photo::part(video) {
  object-fit: cover;
  width: 100%;
  height: 100%;
}

.camera-container capture-photo::part(facing-mode-button) {
  color: #fff;
  cursor: pointer;
  background: #fff3;
  border: none;
  border-radius: 50%;
  justify-content: center;
  align-items: center;
  width: 50px;
  height: 50px;
  display: flex;
  position: absolute;
  bottom: 20px;
  right: 20px;
}

.scan-frame {
  pointer-events: none;
  z-index: 10;
  width: 280px;
  height: 200px;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}

.scan-frame svg {
  width: 100%;
  height: 100%;
}

.scan-instructions {
  text-align: center;
  z-index: 10;
  position: absolute;
  bottom: 100px;
  left: 0;
  right: 0;
}

.scan-instructions p {
  color: #fff;
  background: #000000b3;
  border-radius: 20px;
  padding: 10px 20px;
  font-size: .9rem;
  display: inline-block;
}

.scan-button {
  border: 2px solid var(--success);
  color: #fff;
  cursor: pointer;
  z-index: 20;
  background: #000c;
  border-radius: 12px;
  flex-direction: column;
  align-items: center;
  gap: 10px;
  padding: 20px 30px;
  font-size: 1rem;
  display: flex;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}

.scan-button svg {
  color: var(--success);
}

.zoom-controls {
  z-index: 10;
  background: #0009;
  border-radius: 20px;
  align-items: center;
  gap: 12px;
  padding: 8px 16px;
  display: flex;
  position: absolute;
  bottom: 20px;
  left: 50%;
  transform: translateX(-50%);
}

.zoom-controls button {
  color: #fff;
  cursor: pointer;
  background: #fff3;
  border: none;
  border-radius: 50%;
  justify-content: center;
  align-items: center;
  width: 32px;
  height: 32px;
  font-size: 1.2rem;
  display: flex;
}

.zoom-controls button:active {
  background: #ffffff4d;
}

.toast-container {
  z-index: 200;
  flex-direction: column;
  gap: 8px;
  width: calc(100% - 32px);
  max-width: 400px;
  display: flex;
  position: fixed;
  top: 70px;
  left: 50%;
  transform: translateX(-50%);
}

.toast {
  border-radius: 8px;
  padding: 12px 16px;
  font-size: .9rem;
  animation: .3s slideIn;
}

.toast.success {
  background: var(--success);
  color: #000;
}

.toast.danger {
  background: var(--danger);
  color: #fff;
}

@keyframes slideIn {
  from {
    opacity: 0;
    transform: translateY(-20px);
  }

  to {
    opacity: 1;
    transform: translateY(0);
  }
}

.modal {
  background: none;
  border: none;
  width: 100%;
  max-width: 100%;
  height: 100%;
  max-height: 100%;
  margin: auto;
  padding: 0;
  position: fixed;
  inset: 0;
}

.modal::backdrop {
  background: #000000b3;
}

.modal-content {
  background: var(--bg-darker);
  border-radius: 20px 20px 0 0;
  max-height: 80vh;
  padding: 20px;
  animation: .3s slideUp;
  position: absolute;
  bottom: 0;
  left: 0;
  right: 0;
  overflow-y: auto;
}

@keyframes slideUp {
  from {
    transform: translateY(100%);
  }

  to {
    transform: translateY(0);
  }
}

.modal-header {
  justify-content: space-between;
  align-items: center;
  margin-bottom: 20px;
  display: flex;
}

.modal-header h2 {
  margin: 0;
  font-size: 1.2rem;
  font-weight: 600;
}

.close-btn {
  color: var(--text-secondary);
  cursor: pointer;
  background: none;
  border: none;
  border-radius: 50%;
  justify-content: center;
  align-items: center;
  width: 40px;
  height: 40px;
  display: flex;
}

.close-btn:hover {
  background: #ffffff1a;
}

.settings-form {
  flex-direction: column;
  gap: 12px;
  display: flex;
}

.setting-item {
  cursor: pointer;
  background: #ffffff0d;
  border-radius: 8px;
  align-items: center;
  gap: 12px;
  padding: 12px;
  display: flex;
}

.setting-item input[type="checkbox"] {
  accent-color: var(--success);
  width: 20px;
  height: 20px;
}

.setting-item span {
  flex: 1;
  font-size: 1rem;
}

.product-form {
  flex-direction: column;
  gap: 16px;
  display: flex;
}

.form-group {
  flex-direction: column;
  gap: 6px;
  display: flex;
}

.form-group label {
  color: var(--text-secondary);
  font-size: .9rem;
}

.form-group input {
  border: 1px solid var(--border-color);
  color: var(--text-primary);
  background: #ffffff0d;
  border-radius: 8px;
  padding: 14px 16px;
  font-size: 1rem;
}

.form-group input:focus {
  border-color: var(--success);
  outline: none;
}

.form-group input::placeholder {
  color: var(--text-secondary);
}

.btn-primary {
  background: var(--success);
  color: #000;
  cursor: pointer;
  border: none;
  border-radius: 8px;
  margin-top: 8px;
  padding: 16px;
  font-size: 1rem;
  font-weight: 600;
}

.btn-primary:active {
  opacity: .9;
}

.noscript {
  background: var(--bg-dark);
  color: var(--text-primary);
  text-align: center;
  z-index: 1000;
  justify-content: center;
  align-items: center;
  padding: 20px;
  font-size: 1.1rem;
  display: flex;
  position: fixed;
  inset: 0;
}

.results {
  display: none !important;
}

.visually-hidden {
  clip: rect(0, 0, 0, 0);
  border: 0;
  width: 1px;
  height: 1px;
  margin: -1px;
  padding: 0;
  position: absolute;
  overflow: hidden;
}

@media (width >= 768px) {
  .modal-content {
    border-radius: 12px;
    max-width: 400px;
    margin: auto;
    animation: .2s fadeIn;
    position: relative;
    top: 50%;
    bottom: auto;
    transform: translateY(-50%);
  }

  @keyframes fadeIn {
    from {
      opacity: 0;
    }

    to {
      opacity: 1;
    }
  }

  .scan-frame {
    width: 350px;
    height: 250px;
  }
}

